.mobile-menu__menu-header-wrap {
  z-index: 995;
  display: none;
}

.mobile-menu {
  text-align: -webkit-right;
  z-index: 996;
  display: none;
  grid-area: 1 / 1 / 3 / 4;
}

.menu-btn {
  display: block;
  width: 95px;
  height: 40px;
  position: relative;
}
.menu-btn span, .menu-btn span:before, .menu-btn span:after {
  display: block;
  width: 61px;
  height: 3px;
  background: black;
  position: absolute;
  top: 103%;
  left: 98.7%;
  margin-left: -60px;
  margin-top: -3px;
  transition: all 0.4s;
}
.mobile-menu.menu-active {
  position: fixed;
  background: transparent;
  right: 35px;
  top: 40px;
}


.menu-btn span:before, .menu-btn span:after {
  content: '';
}

.menu-btn span:before {
  transform: translateY(-15px);
}

.menu-btn span:after {
  transform: translateY(15px);
}

.menu-logo-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 80%;
}

.mobile-menu__menu-header-text {
  text-align-last: left;
  margin-right: 29%;
  line-height: 40px;
}

.mobile-menu.menu-active {
  position: fixed;
  background: transparent;
}

.menu-btn.menu-active span {
  background: none!important;
}

.menu-btn.menu-active span::before {
  top: 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.menu-btn.menu-active span::after {
  bottom: 0;
  -webkit-transform: rotate(-45deg);
}

.mobile-menu__menu-header-wrap.menu-active {
  transition: all 0.4s linear;
  opacity: 1;
  position: fixed;
  top: 0;
  right: 0;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  z-index: 95;
}

.mobile-menu__menu-header-wrap,.mobile-menu {
  z-index: 100;
  display: none


}


